	module.exports = {"data":{"markdownRemark":{"html":"<p>Dot picture created for making MMORPG \" <a href=\"/tag/evil-kingdom\">EVIL KINGDOM</a> \" for mobile phones that was being produced at the time. I remember that dot painting was better at drawing than illustration. By the way, this game, initially renewed to TowerQuest style, then to the <a href=\"http://www.e-jam.co.jp/shinto/\">windstorm</a> style, progress has been made to the level that can be said as a prototype version. After that, after renewal like Ragnarok Online, it was frustrating that it was technically impossible to transplant from the mobile version to the PC version, and also the mobile version which had been up to the alpha version was also terminated.</p>","frontmatter":{"cover":null,"date":"2002-01-20T17:05:50+09:00","strDate":"2002/1/20","title":"Dot character"}}},"pathContext":{"path":"/en/blog/2002/01/20-dot-party/","langKey":"en"}}

